@import url(https://fonts.googleapis.com/css?family=Righteous);
@import url(https://fonts.googleapis.com/css?family=Righteous);
body {
	margin: 0;
	padding: 0;
	font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto",
		"Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
		"Helvetica Neue", sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
		monospace;
}

html {
	height: 100%;
}

body {
	margin-top: 4em;
	background: #0f0c29; /* fallback for old browsers */
	background: -webkit-linear-gradient(
		to bottom,
		#24243e,
		#302b63,
		#0f0c29
	); /* Chrome 10-25, Safari 5.1-6 */
	background: -webkit-gradient(
		linear,
		left top, left bottom,
		from(#24243e),
		color-stop(#302b63),
		to(#0f0c29)
	);
	background: -webkit-linear-gradient(
		top,
		#24243e,
		#302b63,
		#0f0c29
	);
	background: linear-gradient(
		to bottom,
		#24243e,
		#302b63,
		#0f0c29
	); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
	text-align: center;
	font-family: "Righteous";
}

h2 {
	color: white;
	text-shadow: 4px 4px 5px black;
}

[class*="drum-pad-"] {
	border-radius: 0.2em;
	color: white;
	width: 5em;
	height: 5em;
	display: flex;
	align-items: center;
	justify-content: center;
	margin: 0.5em;
}

label {
	text-shadow: 4px 4px 6px black;
}
.drum-pad-1,
.drum-pad-2,
.drum-pad-3 {
	border: solid 0.3em rgba(255, 0, 0, 0.5);
	box-shadow: 0 0 0.5em red;
}

.drum-pad-4,
.drum-pad-5,
.drum-pad-6 {
	border: solid 0.3em rgba(0, 0, 255, 0.5);
	box-shadow: 0 0 0.5em blue;
}
.drum-pad-7,
.drum-pad-8,
.drum-pad-9 {
	border: solid 0.3em rgba(255, 255, 0, 0.5);
	box-shadow: 0 0 0.5em yellow;
}

#display {
	display: flex;
	flex-flow: column wrap;
	color: white;
	justify-content: flex-start;
	align-items: center;
	text-align: center;
	padding: 0.5em;
	font-family: "Righteous";
}

#display p {
	margin: 0;
}

.display-panel {
	background-color: rgba(0, 255, 255, 0.651);
	color: black;
	padding: 0.5em;
	border-radius: 0.2em;
	width: 6em;
	box-shadow: 0 0 0.5em cyan;
}

.volume-panel {
	margin-top: 1em;
}

.volume-input {
	margin: 2em;
	width: 4em;
	height: auto;
	-webkit-transform-origin: center;
	        transform-origin: center;
	-webkit-transform: rotate(-90deg);
	        transform: rotate(-90deg);
	background-color: yellow;
}

.switch-panel {
	margin: 1em;
}

.power-panel {
	margin: 2em;
}

.switch {
	position: relative;
	display: inline-block;
	width: 60px;
	height: 34px;
	box-shadow: 0 0 0.5em black;
}

.switch input {
	opacity: 0;
	width: 0;
	height: 0;
}

.slider {
	position: absolute;
	cursor: pointer;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	background-color: gray;
	-webkit-transition: 0.4s;
	transition: 0.4s;
}

.slider:before {
	position: absolute;
	content: "";
	height: 26px;
	width: 26px;
	left: 4px;
	bottom: 4px;
	background-color: black;
	-webkit-transition: 0.4s;
	transition: 0.4s;
}

input:checked + .slider {
	background-color: gray;
}

.power-panel input:checked + .slider {
	background-color: green;
}

.power-panel .slider {
	background-color: red;
}

input:checked + .slider:before {
	-webkit-transform: translateX(26px);
	transform: translateX(26px);
}

.gh-item {
	margin-top: 0.5em;
}
.gh-item a {
	text-decoration: none;
	border-radius: 1em;
	color: blueviolet;
	padding: 0.5em;
	font-weight: bold;
	text-shadow: 2px 2px 1em black;
}

.gh-item i {
	padding: 0.3em;
	background-color: black;
	color: blueviolet;
	border-radius: 100%;
	font-weight: normal !important;
	box-shadow: 0 0 0.5em blueviolet;
}

.container {
	display: flex;
	justify-content: center;
	flex-flow: row;
}

#drum-machine {
	border-radius: 1em;
	background-color: rgba(0, 0, 0, 0.658);
	display: flex;
	padding: 0.5em;
	box-shadow: 0 0 20px 3px rgba(0, 0, 0, 0.75);
}
#pads-panel {
	display: flex;
	flex-flow: row wrap;
	width: 20em;
	font-family: "Righteous";
	font-size: 20px;
	justify-content: center;
}

@media (min-width: 320px) and (max-width: 480px) {
	#drum-machine {
		display: flex;
		flex-flow: column wrap;
		align-items: center;
		width: 100%;
	}

	[class*="drum-pad-"] {
		width: 4em;
		height: 4em;
	}

	#display {
		flex-flow: row wrap;
		justify-content: center;
	}

	.volume-input {
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
		margin: 0;
	}
	.display-panel {
		height: 3em;
		display: flex;
		align-items: center;
		justify-content: center;
	}
	.display-panel,
	.volume-panel,
	.switch-panel,
	.power-panel {
		margin: 0;
		padding: 0.2em;
		flex: 1 1;
	}
}

